import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AboutusComponent } from './aboutus/aboutus.component';
import { CareersComponent } from './careers/careers.component';
import { ContactusComponent } from './contactus/contactus.component';
import { ContractjobsComponent } from './contractjobs/contractjobs.component';
import { Guard1Service } from './guard1.service';
import { Guard2Service } from './guard2.service';
import { Guard3Service } from './guard3.service';
import { HomeComponent } from './home/home.component';
import { NotfoundComponent } from './notfound/notfound.component';
import { PermanentjobsComponent } from './permanentjobs/permanentjobs.component';
import { ProductResolverService } from './product-resolver.service';
import { ProductdetailComponent } from './productdetail/productdetail.component';
import { ProductdetailsComponent } from './productdetails/productdetails.component';
import { ProductsComponent } from './products/products.component';

const routes: Routes = [
  { path: 'home', component: HomeComponent },
  { path: 'aboutus', component: AboutusComponent },
  {
    path: 'careers',
    component: CareersComponent,
    canActivateChild: [Guard2Service],
    children: [
      { path: '', component: PermanentjobsComponent },
      { path: 'permanent', component: PermanentjobsComponent },
      { path: 'contract', component: ContractjobsComponent },
    ],
  },
  {
    path: 'contactus',
    component: ContactusComponent,
    canDeactivate: [Guard3Service],
  },
  {
    path: 'products',
    component: ProductsComponent,
    canActivate: [Guard1Service],
  },
  {
    path: 'productdetails/:id',
    component: ProductdetailsComponent,
    resolve: { productDetail: ProductResolverService },
  },
  { path: 'productdetail', component: ProductdetailComponent },
  { path: 'customer', loadChildren: () => import('./customers/customers.module').then(m => m.CustomersModule) },
  { path: '', component: HomeComponent },
  { path: '**', component: NotfoundComponent }, // wild card route
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule],
})
export class AppRoutingModule {}
